import {Component, OnInit, Input, Output, EventEmitter} from "@angular/core";

@Component({
  selector: 'ob-todo-list-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
  @Input() title: string;
  @Input() inputPlaceholder: string;
  @Output() newItemEvent = new EventEmitter<string>();

  private _newItemTitle: string;

  constructor() {

  }

  get newItemTitle(): string {
    return this._newItemTitle;
  }

  set newItemTitle(value: string) {
    this._newItemTitle = value;
  }

  public emitNewItem(): void {
    this.newItemEvent.emit(this.newItemTitle);
    this.newItemTitle = '';
  }

  ngOnInit() {
  }

}



