import {Injectable} from "@angular/core";
import {Item} from "./item";

@Injectable()
export class TodoServiceService {
  private _items: Item[];

  constructor() {
    this.items = [];
  }

  get items(): Item[] {
    return this._items;
  }

  set items(value: Item[]) {
    this._items = value;
  }

  public newItem(itemTitle: string): void {
    this._items.push(new Item(itemTitle));
  }

  public deleteItem(itemToDel: Item): void {
    console.log('deleting');
    this._items.splice(this.items.indexOf(itemToDel), 1);
  }
}



