import {Component, Input} from "@angular/core";
import {Item} from "../item";

@Component({
  selector: 'ob-todo-list-todo-list',
  template: `
    <ul class="todo-list">
      <ob-todo-list-list-item *ngFor="let item of items" [list-item]="item"></ob-todo-list-list-item>
    </ul>
  `,
  styles: []
})
export class TodoListComponent {
  @Input() items: Item[];

  constructor() {
  }
}



